.preview-toolbar {
  width: 210mm;
  margin: 0 auto;
}

.preview-spinner {
  width: 210mm;
  margin: 0 auto;
  text-align: center;
}

embed {
  width: 210mm;
  height: 297mm;
  margin: 0 auto;
}

object {
  width: 210mm;
  height: 297mm;
  margin: 0 auto;
}

.no-print {
  display: block;
}

@media print {
  .no-print {
    display: none;
  }
}

textarea {
  overflow-wrap: normal;
  white-space: nowrap;
}

